#work-order-details-card {
	max-width: unset;
	height: 85vh;
	width: 90vw;
}

#work-order-details-card>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}

#work-order-information {
	height: 25%;
}

#work-order-tabs {
	height: 65%;
	padding: 10px;
}

#work-order-actions {
	height: 10%;
	padding-top: 0px;
}
#work-order-actions>.action-row {
	height: 100%;
}

/*WORK ORDER ACTIONS*/
.work-order-actions-button {
	height: 100% !important;
	font-size: x-large;
	line-height: calc(200% + 10px);
}

/*WORK ORDER TABS*/
#work-order-tabs-card {
	min-height: unset;
	max-width: unset;
	height: 100%;
	width: 100%;
	overflow: hidden;
	background-color: #fafafa !important;
}
#work-order-tabs-card>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}
#work-order-tabs-card-card-content>.menu-bar {
	display: flex;
	height: 40px !important;
}
#work-order-tabs-card-card-content>.menu-bar>.menu-button {
	height: 40px;
	line-height: 40px;
	border-bottom: 1px solid #ccc;
}
/*Style buttons*/
#work-order-tabs-card-card-content>.menu-bar>:nth-child(1) {
	background-color: #3f8d8d;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(1):hover,
#work-order-tabs-card-card-content>.menu-bar>:nth-child(1).selected {
	background-color: #275c5c;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(2) {
	background-color: #8d3f3f;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(2):hover,
#work-order-tabs-card-card-content>.menu-bar>:nth-child(2).selected {
	background-color: #5c2727;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(3) {
	background-color: #3f8d60;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(3):hover,
#work-order-tabs-card-card-content>.menu-bar>:nth-child(3).selected {
	background-color: #296442;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(4) {
	background-color: #7b3f8d;
}
#work-order-tabs-card-card-content>.menu-bar>:nth-child(4):hover,
#work-order-tabs-card-card-content>.menu-bar>:nth-child(4).selected {
	background-color: #592e66;
}

/*WORK ORDER INFO SECTION*/
.work-order-info-cont {
	height: 100%;
	display: grid;
	grid-template-columns: 40% 60%;
}

#notification-cont {
	border: 1px solid #ccc;
	background-color: #efefef;
	border-radius: 10px;
	height: 100%;
	width: 100%;
	padding: 10px;
	display: flex;
	flex-direction: column;
	gap: 10px;
	overflow-y: scroll;
}
.wo-info-cont {
	overflow-y: auto;
}
.wo-cust-code {
	font-size: xx-large;
	text-align: left;
	font-weight: 700;
}
.wo-addy {
	font-size: large;
	font-weight: 600;
	font-style: italic;
	text-align: left;
}
.wo-descr {
	padding: 5px;
	margin: 5px;
	margin-right: 20px;
	margin-bottom: 0px;
	text-align: left;
	background-color: #efefef;
	border-radius: 10px;
	border: 1px solid #ccc;
	height: 62.5%;
	overflow-y: auto;
}

/*CLOSE OUT WORK ORDER*/
#close-actions-yes,#close-actions-no {
	height: 50px;
	line-height: 50px;
	font-size: x-large;
}
#close-actions-yes {
	background-color: #3f8d60;
}
#close-actions-yes:hover {
	background-color: #296442;
}

#sitem-detail-float-cont {
	top: -7.5vh !important;
	left: -5vw !important;
}

/*FORMS*/
.ticket-detail-form-cont {
	padding: 10px;
	padding-left: 20px;
	padding-right: 20px;
	height: 100%;
	overflow-y: auto;
}
.form-header {
	font-size: 30px;
	display: grid;
	grid-template-columns: 100% 0%;
	font-weight: 700;
}
.ticket-detail-form-item {
	display: grid !important;
	grid-template-columns: 60% 40% !important;
	width: 50%;
	padding: 10px;
}
.ticket-detail-form-item>.form-info-title {
	font-size: 25px;
}
.ticket-detail-form-item>:nth-child(2) {
	font-size: 25px;
}
.ticket-detail-form-item>.action-button {
	background-color: transparent;
	filter: unset;
	color: #3f8d60;
	font-weight: 700;
	font-size: 20px;
}
.ticket-detail-form-item>.action-button:hover {
	color: #296442;
}

/*INVOICE FORM*/
#savings-form-item-cont>:nth-child(2)::before,
#total-form-item-cont>:nth-child(2)::before {
	content: '$';
}

/*REPAIR FORM*/
.form-repair-item {
	display: grid;
	height: fit-content;
	grid-template-columns:20% 65% 10% 5%;
	padding: 10px;
	font-size: 22.5px;
	gap: 10px;
	text-align: left;
	width: 70%;
}
.repair-price::before {
	content: '$';
}

/*TECH FORM*/
.tech-form-item {
	text-align: left;
	font-size: 20px;
	padding: 10px;
}