/*COLLATERAL*/
#collat-cont {
	background-color: rgba(0, 0, 0, 0.3);
}

.collateral-card {
	min-width: 90vw;
	max-width: 90vw;
	max-height: 90vh;
	min-height: 90vh;
}

.collateral-cont {
	min-height: 100% !important;
	max-height: 100% !important;
	background-color: #f0f0f0;
	max-width: 100%;
}

.collateral-card>.card-content {
	display: grid;
	grid-template-columns: 70% 30%;
	height: calc(100% - 40px);
}
.collateral-cont>.card-content {
	height: 100%;
	display: flex;
	flex-direction: column;
	gap: 15px;
	align-items: center;
	overflow-y: scroll;
	scrollbar-gutter: 8px;
}

.actions-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
	align-items: center;
	justify-content: end;
	margin-left: 10px;
}
.actions-cont>.action-button {
	width: 100%;
	height: 50px;
	font-size: x-large;
	line-height: 50px;
}
#complete-ticket-button {
	font-size: large;
}
/*Customize Actions Colors*/
#download-invoice-button {
	background-color: #2d7761;
}
#download-invoice-button:hover {
	background-color: #22614e;
}
#email-invoice-button {
	background-color: #2d3e77;
}
#email-invoice-button:hover {
	background-color: #22305e;
}
#collect-payment-button {
	background-color: #77592d;
}
#collect-payment-button:hover {
	background-color: #5f4723;
}
/*@todo*/
.collateral-print-page {
	background-color: #fff;
	min-width: 95%;
	max-width: 95%;
	max-height: 170.5% !important;
	min-height: 170.5% !important;
	padding: 20px;
	filter: drop-shadow(0.025px 0.5px 1.05px rgba(0, 0, 0, 0.3));
	overflow: hidden;
}

/*2 Paths Invoice page*/
.collateral-cont>.card-content>#invoice-cont {
	display: flex;
	flex-direction: column;
}

.collateral-cont>.card-content>.summary-header {
	width: 100%;
}

/*Emailer*/
#emailer-card {
	width: fit-content;
	min-height: unset;
	height: fit-content;
}

.email-text {
	font-size: x-large;
	text-align: left;
	padding: 5px;
}

#emailer-card-card-content>input {
	width: 50%;
	height: 40px;
}
#emailer-card-card-content>.action-row {
	padding-top: 10px;
}

/*Ticket Close Card*/
#ticket-close-card {
	width: max-content;
	height: fit-content;
	min-height: unset;
}
#ticket-close-card-card-content>.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 10px;
	width: 90%;
	margin-left: 5%;
	margin-right: 5%;
	padding: 10px;
}
#ticket-close-card-card-content>.form>.form-info-item {
	display: grid;
	grid-template-columns: 60% 39%;
	gap: 1%;
	width: 100%;
}
#ticket-close-card-card-content>.form>.form-info-item>.form-info-title {
	font-size: 25px;
	font-weight: 600;
}

#close-ticket-button {
	font-size: larger;
	height: fit-content;
	padding: 10px;
	width: 300px;
}
#close-ticket-button:hover {
	text-transform: uppercase;
	font-weight: 650;
}

div.block {
	background-color: #22614e00;
	width: 100px;
	height: 100px;
	height: 100%;
	width: 100%;
	position: absolute;
	z-index: 200;
}

@media screen and (orientation:portrait) {
	.collateral-card {
		min-width: 95vw !important;
		max-width: 95vw !important;
	}
	.collateral-card>.card-content {
		display: flex;
		flex-direction: column;
		height: calc(100% - 40px);
	}
	.collateral-cont {
		min-height: 90% !important;
		max-height: 90% !important;
		height: 90% !important;
	}
	.actions-cont {
		display: flex;
		flex-direction: row;
		height: 10%;
	}
	.actions-cont>.action-button {
		font-size: medium;
	}
	.actions-cont>:nth-child(3) {
		font-size: small !important;
	}
	.actions-cont>:nth-child(4) {
		font-size: x-small !important;
	}
}