#wo-table-card,
.ticket-table-card {
	height: 99%;
	max-width: unset !important;
	width: 99.5%;
	overflow: hidden;
}

.ticket-table-card>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}

.ticket-table-card>.card-content>.tablerow.header {
	height: 40px;
	background-color: #efefef;
	padding: 0px;
	margin: 0px;
	line-height: 40px;
	font-weight: 600;
	grid-template-columns: 16% 16% 16% 16% 16% 20%;
	border-bottom: 1px solid #ccc;
}

.ticket-table-card>.card-content>.tablerow.header>div {
	border-right: 1px solid #ccc;
	margin: 0px;
	padding: 0px;
	background-color: transparent;
}

.complete-wo-tablerow,
.ticket-tablerow {
	height: fit-content;
	border-bottom: 1px solid #ccc;
	grid-template-columns: 16% 16% 16% 16% 16% 20%;
	font-size: x-large;
	align-items: center;
}
.complete-wo-tablerow:hover,
.ticket-tablerow:hover {
	background-color: #fafafa;
}
.ticket-tablerow>.action-row {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.ticket-table-card-table {
	height: calc(100% - 40px) !important;
}

.view-invoice-button {
	background-color: #964fb3;
}
.view-invoice-button:hover {
	background-color: #7c3f94;
}

.view-details-button {
	background-color: #53bb76;
}
.view-details-button:hover {
	background-color: #419b5f;
}