.notification {
	display: grid;
	grid-template-columns: 96% 4%;
	padding: 0px;
	background-color: #fefefe;
	border-radius: 5px;
	border: 1px solid #ccc;
	border-bottom: 2px solid #ccc;
	align-items: center;
	overflow: hidden;
	min-height: 40px !important;
	max-height: 40px !important;
}
.notification>.notification-info {
	display: flex;
	align-items: center;
	gap: 10px;
	height: 100%;
}
.notification.low {
	border-bottom: 2px solid #2fa765;
}
.notification.low>.notification-info>img {
	filter: invert(52%) sepia(81%) saturate(351%) hue-rotate(94deg) brightness(89%) contrast(94%);
}
.notification.low>.notification-info>.notification-text {
	color:#2fa765;
}
.notification.medium {
	border-bottom: 2px solid #2f37a7;
}
.notification.medium>.notification-info>img {
	filter: invert(17%) sepia(45%) saturate(6421%) hue-rotate(237deg) brightness(75%) contrast(88%);
}
.notification.medium>.notification-info>.notification-text {
	color:#2f37a7;
}
.notification.high {
	border-bottom: 2px solid #c55c06;
}
.notification.high>.notification-info>img {
	filter: invert(45%) sepia(51%) saturate(6969%) hue-rotate(19deg) brightness(95%) contrast(95%);
}
.notification.high>.notification-info>.notification-text {
	color:#c55c06;
}
.notification.urgent {
	border-bottom: 2px solid #a72f2f;
}
.notification.urgent>.notification-info>img {
	filter: invert(22%) sepia(16%) saturate(7342%) hue-rotate(337deg) brightness(101%) contrast(90%);
}
.notification.urgent>.notification-info>.notification-text {
	color:#a72f2f;
}
.notification>.notification-info>img {
	width: 20px;
	height: 20px;
}
.notification.low>.notification-dismiss {
	background-color: #2fa765;
}
.notification.medium>.notification-dismiss {
	background-color: #2f37a7;
}
.notification.high>.notification-dismiss {
	background-color: #c55c06;
}
.notification.urgent>.notification-dismiss {
	background-color: #a72f2f;
}
.notification-dismiss {
	width: 100%;
	height: 100%;
	font-size: large;
	filter: unset;
	padding: 5px;
	border-radius: 0px;
	border-top-left-radius: 0px;
	border-bottom-left-radius: 0px;
}
.notification.low>.notification-dismiss:hover,
.notification.medium>.notification-dismiss:hover,
.notification.high>.notification-dismiss:hover,
.notification.urgent>.notification-dismiss:hover,
.notification-dismiss:hover {
	background-color: #ee3131;
}
.notification-info {
	padding: 10px;
	line-height: 20px;
}
.notification-text {
	font-weight: 600;
}